<template>
    <div class="articles-carousel">
        <swiper :slides-per-view="1" :space-between="10" navigation pagination>
        <swiper-slide v-for="article in articles" :key="article.id" @click="scrollToArticle(article.id)">
            <div class="article-item">
                <h3 class="three-d-text">{{ issue.title }}</h3>
            </div>
        </swiper-slide>
        </swiper>
    </div>
</template>

<script>
import {Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/swiper-bundle.css';

export default {
    components: {
        Swiper,
        SwiperSlide
    },
    props: {
        issues: Array,
        required: true
    },
    methods: {
        selectIssue(issue) {
            this.$emit('issue-selected', issue);
        }
    }
                
            

};
</script>

