.fixed-carousel {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 101;
}
.articles-carousel {
    width: 100%;
    height: auto;
    cursor: pointer;
}

.article-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 200px; /*adjust as needed*/
}
.three-d-text {
    font-size: 3em;
    color: #000000;
    text-shadow:
        1px 1px 0 #ab0101,
        2px 2px 0 #ab0101,
        3px 3px 0 #747472;/*create 3d effect */
